@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primary-color: #6317a7;
  --secondary-color: #ed4e94;
  --text-color: #343434;
  --link-color: #6317a7;
  --heading-color: #343434;
  --yellow-color: #fbbb0b;
}

@variants responsive {
  .masonry-6-col {
    column-count: 6;
    column-gap: 1em;
  }
  .masonry-5-col {
    column-count: 5;
    column-gap: 1em;
  }
  .masonry-4-col {
    column-count: 4;
    column-gap: 1em;
  }
  .masonry-3-col {
    column-count: 3;
    column-gap: 1em;
  }
  .masonry-2-col {
    column-count: 2;
    column-gap: 1em;
  }
  .masonry-1-col {
    column-count: 1;
    column-gap: 1em;
  }
  .break-inside {
    break-inside: avoid;
  }
}
